rgin-top: var(--vspace-1);\n}\n\nfigcaption {\n    font-size: var(--text-sm);\n    text-align: center;\n    margin-bottom: 0;\n}\n\nvar, kbd, samp, code, pre {\n    font-family: var(--font-mono);\n}\n\npre {\n    padding: var(--vspace-0_75) var(--vspace-1) var(--vspace-1);\n    background: var(--color-gray-1);\n    overflow-x: auto;\n}\n\ncode {\n    font-size: var(--text-sm);\n    line-height: 1.6rem;\n    margin: 0 .2rem;\n    padding: calc(((var(--vspace-1) - 1.6rem) / 2) - .1rem) calc(.8rem - .1rem);\n    white-space: nowrap;\n    background: var(--color-gray-1);\n    border: 1px solid var(--color-gray-3);\n    color: var(--color-text);\n    border-radius: 3px;\n}\n\npre>code {\n    display: block;\n    white-space: pre;\n    line-height: var(--vspace-1);\n    padding: 0;\n    margin: 0;\n    border: none;\n}\n\ndel {\n    text-decoration: line-through;\n}\n\nabbr {\n    font-family: var(--font-1);\n    font-weight: 400;\n    font-variant: small-caps;\n    text-transform: lowercase;\n    letter-spacing: .1em;\n}\n\nabbr[title], dfn[title] {\n    border-bottom: 1px dotted;\n    cursor: help;\n    text-decoration: none;\n}\n\nmark {\n    background: var(--color-notice);\n    color: var(--color-black);\n}\n\nhr {\n    border: solid var(--color-border);\n    border-width: .1rem 0 0;\n    clear: both;\n    margin: var(--vspace-2) 0 calc(var(--vspace-2) - .1rem);\n    height: 0;\n}\n\nhr.fancy {\n    border: none;\n    margin: var(--vspace-2) 0;\n    height: var(--vspace-1);\n    text-align: center;\n}\n\nhr.fancy::before {\n    content: \"*****\";\n    letter-spacing: .3em;\n}\n\n\n/* -------------------------------------------------------------------\n * ## lists\n * ------------------------------------------------------------------- */\nol {\n    list-style: decimal;\n}\n\nul {\n    list-style: disc;\n}\n\nli {\n    display: list-item;\n}\n\nol, ul {\n    margin-left: 1.6rem;\n}\n\nul li {\n    padding-left: .4rem;\n}\n\nul ul, ul ol, ol ol, ol ul {\n    margin: 1.6rem 0 1.6rem 1.6rem;\n}\n\nul.disc li {\n    display: list-item;\n    list-style: none;\n    padding: 0 0 0 .8rem;\n    position: relative;\n}\n\nul.disc li::before {\n    content: \"\";\n    display: inline-block;\n    width: var(--vspace-0_25);\n    height: var(--vspace-0_25);\n    border-radius: 50%;\n    background: var(--color-1-dark);\n    position: absolute;\n    left: -.9em;\n    top: .65em;\n    vertical-align: middle;\n}\n\ndt {\n    margin: 0;\n    color: var(--color-1);\n}\n\ndd {\n    margin: 0 0 0 2rem;\n}\n\n/* -------------------------------------------------------------------\n * ## responsive video container\n * ------------------------------------------------------------------- */\n.video-container {\n    position: relative;\n    padding-bottom: 56.25%;\n    height: 0;\n    overflow: hidden;\n}\n\n.video-container iframe,\n.video-container object,\n.video-container embed,\n.video-container video {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n}\n\n/* -------------------------------------------------------------------\n * ## floated image\n * ------------------------------------------------------------------- */\n\nimg.h-pull-right {\n    margin: var(--vspace-0_5) 0 var(--vspace-0_5) 2.8rem;\n}\n\nimg.h-pull-left {\n    margin: var(--vspace-0_5) 2.8rem var(--vspace-0_5) 0;\n}\n\n/* -------------------------------------------------------------------\n * ## tables\n * ------------------------------------------------------------------- */\ntable {\n    border-width: 0;\n    width: 100%;\n    max-width: 100%;\n    font-family: var(--font-1);\n    border-collapse: collapse;\n}\n\nth, td {\n    padding: var(--vspace-0_5) 3.2rem calc(var(--vspace-0_5) - .1rem);\n    text-align: left;\n    border-bottom: 1px solid var(--color-border);\n}\n\nth {\n    padding: var(--vspace-0_5) 3.2rem;\n    color: var(--color-text-dark);\n    font-family: var(--font-2);\n    font-weight: 600;\n}\n\nth:first-child, td:first-child {\n    padding-left: 0;\n}\n\nth:last-child, td:last-child {\n    padding-right: 0;\n}\n\n.table-responsive {\n    overflow-x: auto;\n    -webkit-overflow-scrolling: touch;\n}\n\n/* -------------------------------------------------------------------\n * ## spacing\n * ------------------------------------------------------------------- */\nfieldset,\nbutton,\n.btn {\n    margin-bottom: var(--vspace-0_5);\n}\n\ninput,\ntextarea,\nselect,\npre,\nblockquote,\nfigure,\nfigcaption,\ntable,\np,\nul,\nol,\ndl,\nform,\nimg,\n.video-container,\n.ss-custom-select {\n    margin-bottom: var(--vspace-1);\n}\n\n.folders {\n    padding: 0;\n    margin: 0;\n}\n\n\n/* ===================================================================\n * # preloader\n *\n * ------------------------------------------------------------------- */\n#preloader {\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    display: -ms-flexbox;\n    display: -webkit-box;\n    display: flex;\n    -ms-flex-flow: row wrap;\n    -webkit-box-orient: horizontal;\n    -webkit-box-direction: normal;\n    flex-flow: row wrap;\n    -ms-flex-pack: center;\n    -webkit-box-pack: center;\n    justify-content: center;\n    -ms-flex-line-pack: center;\n    align-content: center;\n    background: white;\n    z-index: 500;\n    height: 100vh;\n    width: 100%;\n    opacity: 1;\n}\n\n.no-js #preloader, .oldie #preloader {\n    display: none;\n}\n\n#loader {\n    width: var(--vspace-1_5);\n    height: var(--vspace-1_5);\n    padding: 0;\n    opacity: 1;\n}\n\n#loader:before {\n    content: \"\";\n    border-top: 6px solid rgba(0, 0, 0, 0.2);\n    border-right: 6px solid rgba(0, 0, 0, 0.2);\n    border-bottom: 6px solid rgba(0, 0, 0, 0.2);\n    border-left: 6px solid black;\n    -webkit-animation: load 1.1s infinite linear;\n    animation: load 1.1s infinite linear;\n    display: block;\n    border-radius: 50%;\n    width: var(--vspace-1_5);\n    height: var(--vspace-1_5);\n}\n\n@-webkit-keyframes load {\n    0% {\n        -webkit-transform: rotate(0deg);\n        transform: rotate(0deg);\n    }\n    100% {\n        -webkit-transform: rotate(360deg);\n        transform: rotate(360deg);\n    }\n}\n\n@keyframes load {\n    0% {\n        -webkit-transform: rotate(0deg);\n        transform: rotate(0deg);\n    }\n    100% {\n        -webkit-transform: rotate(360deg);\n        transform: rotate(360deg);\n    }\n}\n\n/* -------------------------------------------------------------------\n * ## page loaded\n * ------------------------------------------------------------------- */\n.ss-loaded #preloader {\n    opacity: 0;\n    visibility: hidden;\n    -webkit-transition: all .6s .9s ease-in-out;\n    transition: all .6s .9s ease-in-out;\n}\n\n.ss-loaded #preloader #loader {\n    opacity: 0;\n    -webkit-transition: opacity .6s ease-in-out;\n    transition: opacity .6s ease-in-out;\n}\n\n\n\n/* ===================================================================\n * # forms\n *\n * ------------------------------------------------------------------- */\n.form-field {\n    display: flex;\n    flex-direction: row;\n}\n\nfieldset {\n    border: none;\n}\n\ninput[type=\"email\"],\ninput[type=\"number\"],\ninput[type=\"search\"],\ninput[type=\"text\"],\ninput[type=\"tel\"],\ninput[type=\"url\"],\ninput[type=\"password\"],\ninput[type=\"file\"],\ntextarea,\nselect {\n    --input-height: var(--vspace-2);\n    --input-line-height: var(--vspace-1);\n    --input-vpadding: calc(((var(--input-height) - var(--input-line-height)) / 2) - .1rem);\n    display: block;\n    height: var(--input-height);\n    padding: var(--input-vpadding) calc(2.4rem - .1rem);\n    border: 0;\n    outline: none;\n    color: var(--color-placeholder);\n    font-family: var(--font-1);\n    font-size: var(--text-sm);\n    line-height: var(--input-line-height);\n    max-width: 100%;\n    background-color: rgba(0, 0, 0, 0.07);\n    border: 1px solid transparent;\n    -webkit-transition: all .3s ease-in-out;\n    transition: all .3s ease-in-out;\n}\n\n.ss-custom-select {\n    position: relative;\n    padding: 0;\n}\n\n.ss-custom-select select {\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    appearance: none;\n    text-indent: 0.01px;\n    text-overflow: '';\n    margin: 0;\n    vertical-align: middle;\n}\n\n.ss-custom-select select option {\n    padding-left: 2rem;\n    padding-right: 2rem;\n}\n\n.ss-custom-select select::-ms-expand {\n    display: none;\n}\n\n.ss-custom-select::after {\n    border-bottom: 2px solid black;\n    border-right: 2px solid black;\n    content: '';\n    display: block;\n    height: 8px;\n    width: 8px;\n    margin-top: -7px;\n    pointer-events: none;\n    position: absolute;\n    right: 2.4rem;\n    top: 50%;\n    -webkit-transition: all 0.15s ease-in-out;\n    transition: all 0.15s ease-in-out;\n    -webkit-transform-origin: 66% 66%;\n    transform-origin: 66% 66%;\n    -webkit-transform: rotate(45deg);\n    transform: rotate(45deg);\n}\n\ntextarea {\n    min-height: calc(7 * var(--space));\n}\n\ninput[type=\"email\"]:focus,\ninput[type=\"number\"]:focus,\ninput[type=\"search\"]:focus,\ninput[type=\"text\"]:focus,\ninput[type=\"tel\"]:focus,\ninput[type=\"url\"]:focus,\ninput[type=\"password\"]:focus,\ninput[type=\"file\"]:focus,\ntextarea:focus,\nselect:focus {\n    color: white;\n    background-color: var(--color-gray-16);\n    border: 1px solid var(--color-gray-16);\n}\n\nlabel, legend {\n    font-family: var(--font-1);\n    font-weight: 600;\n    font-size: var(--text-sm);\n    line-height: var(--vspace-0_5);\n    margin-bottom: var(--vspace-0_5);\n    color: var(--color-text-dark);\n    display: block;\n}\n\ninput[type=\"checkbox\"],\ninput[type=\"radio\"] {\n    display: inline;\n}\n\nlabel>.label-text {\n    display: inline-block;\n    margin-left: 1rem;\n    font-family: var(--font-1);\n    line-height: inherit;\n}\n\nlabel>input[type=\"checkbox\"], label>input[type=\"radio\"] {\n    margin: 0;\n    position: relative;\n    top: .2rem;\n}\n\n/* -------------------------------------------------------------------\n * ## style placeholder text\n * ------------------------------------------------------------------- */\n::-webkit-input-placeholder {\n    /* WebKit, Blink, Edge */\n    color: var(--color-placeholder);\n}\n\n:-ms-input-placeholder {\n    /* Internet Explorer 10-11 */\n    color: var(--color-placeholder);\n}\n\n::-ms-input-placeholder {\n    /* Microsoft Edge */\n    color: var(--color-placeholder);\n}\n\n::placeholder {\n    /* Most modern browsers support this now. */\n    color: var(--color-placeholder);\n}\n\n.placeholder {\n    color: var(--color-placeholder) !important;\n}\n\n/* -------------------------------------------------------------------\n * ## change autocomplete styles in Chrome\n * ------------------------------------------------------------------- */\ninput:-webkit-autofill,\ninput:-webkit-autofill:hover,\ninput:-webkit-autofill:focus input:-webkit-autofill,\ntextarea:-webkit-autofill,\ntextarea:-webkit-autofill:hover textarea:-webkit-autofill:focus,\nselect:-webkit-autofill,\nselect:-webkit-autofill:hover,\nselect:-webkit-autofill:focus {\n    -webkit-text-fill-color: var(--color-1);\n    -webkit-transition: background-color 5000s ease-in-out 0s;\n    transition: background-color 5000s ease-in-out 0s;\n}\n\n\n\n/* ===================================================================\n * # buttons\n *\n * ------------------------------------------------------------------- */\n.btn,\nbutton,\ninput[type=\"submit\"],\ninput[type=\"reset\"],\ninput[type=\"button\"] {\n    --btn-height: var(--vspace-btn);\n    display: inline-block;\n    font-family: var(--font-2);\n    font-weight: 600;\n    font-size: var(--text-xs);\n    text-transform: uppercase;\n    letter-spacing: .4em;\n    height: var(--btn-height);\n    line-height: calc(var(--btn-height) - .4rem);\n    padding: 0 3.2rem;\n    margin: 0 .4rem 1.6rem 0;\n    color: var(--color-btn-text);\n    text-decoration: none;\n    text-align: center;\n    white-space: nowrap;\n    cursor: pointer;\n    -webkit-transition: all .3s;\n    transition: all .3s;\n    -webkit-font-smoothing: antialiased;\n    -moz-osx-font-smoothing: grayscale;\n    background-color: var(--color-btn);\n    border: 0.2rem solid var(--color-btn);\n}\n\n.btn:hover,\nbutton:hover,\ninput[type=\"submit\"]:hover,\ninput[type=\"reset\"]:hover,\ninput[type=\"button\"]:hover,\n.btn:focus, button:focus,\ninput[type=\"submit\"]:focus,\ninput[type=\"reset\"]:focus,\ninput[type=\"button\"]:focus {\n    background-color: var(--color-btn-hover);\n    border-color: var(--color-btn-hover);\n    color: var(--color-btn-hover-text);\n    outline: 0;\n}\n\n/* button primary\n * ------------------------------------------------- */\n.btn.btn--primary,\nbutton.btn--primary,\ninput[type=\"submit\"].btn--primary,\ninput[type=\"reset\"].btn--primary,\ninput[type=\"button\"].btn--primary {\n    background: var(--color-btn-primary);\n    border-color: var(--color-btn-primary);\n    color: var(--color-btn-primary-text);\n}\n\n.btn.btn--primary:hover,\nbutton.btn--primary:hover,\ninput[type=\"submit\"].btn--primary:hover,\ninput[type=\"reset\"].btn--primary:hover,\ninput[type=\"button\"].btn--primary:hover,\n.btn.btn--primary:focus,\nbutton.btn--primary:focus,\ninput[type=\"submit\"].btn--primary:focus,\ninput[type=\"reset\"].btn--primary:focus,\ninput[type=\"button\"].btn--primary:focus {\n    background: var(--color-btn-primary-hover);\n    border-color: var(--color-btn-primary-hover);\n    color: var(--color-btn-primary-hover-text);\n}\n\n/* button modifiers\n * ------------------------------------------------- */\n.btn.h-full-width, button.h-full-width {\n    width: 100%;\n    margin-right: 0;\n}\n\n.btn--small, button.btn--small {\n    --btn-height: calc(var(--vspace-btn) - 1.6rem);\n}\n\n.btn--medium, button.btn--medium {\n    --btn-height: calc(var(--vspace-btn) + .8rem);\n}\n\n.btn--large, button.btn--large {\n    --btn-height: calc(var(--vspace-btn) + 1.6rem);\n}\n\n.btn--stroke, button.btn--stroke {\n    background: transparent !important;\n    border: 0.2rem solid var(--color-btn-stroke);\n    color: var(--color-btn-stroke-text);\n}\n\n.btn--stroke:hover, button.btn--stroke:hover {\n    background: var(--color-btn-stroke-hover) !important;\n    border: 0.2rem solid var(--color-btn-stroke-hover);\n    color: var(--color-btn-stroke-hover-text);\n}\n\n.btn--pill, button.btn--pill {\n    padding-left: 3.2rem !important;\n    padding-right: 3.2rem !important;\n    border-radius: 1000px !important;\n}\n\nbutton::-moz-focus-inner, input::-moz-focus-inner {\n    border: 0;\n    padding: 0;\n}\n\n\n\n/* ===================================================================\n * # additional components\n *\n * ------------------------------------------------------------------- */\n\n/* -------------------------------------------------------------------\n * ## additional typo styles\n * ------------------------------------------------------------------- */\n.drop-cap:first-letter {\n    float: left;\n    font-family: var(--font-2);\n    font-weight: 700;\n    font-size: calc(3 * var(--space));\n    line-height: 1;\n    padding: 0 0.125em 0 0;\n    text-transform: uppercase;\n    background: transparent;\n    color: var(--color-text-dark);\n}\n\n/* line definition style\n * ----------------------------------------------- */\n.lining dt, .lining dd {\n    display: inline;\n    margin: 0;\n}\n\n.lining dt+dt:before, .lining dd+dt:before {\n    content: \"\\A\";\n    white-space: pre;\n}\n\n.lining dd+dd:before {\n    content: \", \";\n}\n\n.lining dd+dd:before {\n    content: \", \";\n}\n\n.lining dd:before {\n    content: \": \";\n    margin-left: -0.2em;\n}\n\n/* dictionary definition style\n * ----------------------------------------------- */\n.dictionary-style dt {\n    display: inline;\n    counter-reset: definitions;\n}\n\n.dictionary-style dt+dt:before {\n    content: \", \";\n    margin-left: -0.2em;\n}\n\n.dictionary-style dd {\n    display: block;\n    counter-increment: definitions;\n}\n\n.dictionary-style dd:before {\n    content: counter(definitions, decimal) \". \";\n}\n\n/**\n * Pull Quotes\n * -----------\n * markup:\n *\n * <figure class=\"pull-quote\">\n *\t\t<blockquote>\n *\t\t\t<p></p>\n *\t\t</blockquote>\n * </figure>\n *\n * --------------------------------------------------------------------- */\n.pull-quote {\n    position: relative;\n    padding: 0;\n    margin-top: 0;\n    text-align: center;\n}\n\n.pull-quote blockquote {\n    border: none;\n    margin: 0 auto;\n    max-width: 62rem;\n    padding-top: var(--vspace-2_5);\n    position: relative;\n}\n\n.pull-quote blockquote p {\n    font-weight: 400;\n    color: var(--color-text-dark);\n}\n\n.pull-quote blockquote:before {\n    content: \"\";\n    display: block;\n    height: var(--vspace-1);\n    width: var(--vspace-1);\n    background-repeat: no-repeat;\n    background: center center;\n    background-size: contain;\n    /*background-image: url(../images/icons/icon-quote.svg);*/\n    -webkit-transform: translate3d(-50%, 0, 0);\n    transform: translate3d(-50%, 0, 0);\n    position: absolute;\n    top: var(--vspace-1);\n    left: 50%;\n}\n\n/**\n * Stats Tab\n * ---------\n * markup:\n *\n * <ul class=\"stats-tabs\">\n *\t\t<li><a href=\"#\">[value]<em>[name]</em></a></li>\n *\t</ul>\n *\n * Extend this object into your markup.\n *\n * --------------------------------------------------------------------- */\n.stats-tabs {\n    padding: 0;\n    margin: var(--vspace-1) 0;\n}\n\n.stats-tabs li {\n    display: inline-block;\n    margin: 0 1.6rem var(--vspace-0_5) 0;\n    padding: 0 1.5rem 0 0;\n    border-right: 1px solid var(--color-border);\n}\n\n.stats-tabs li:last-child {\n    margin: 0;\n    padding: 0;\n    border: none;\n}\n\n.stats-tabs li a {\n    display: inline-block;\n    font-size: var(--text-lg);\n    font-family: var(--font-2);\n    font-weight: 600;\n    line-height: var(--vspace-1_5);\n    border: none;\n    color: var(--color-black);\n}\n\n.stats-tabs li a:hover {\n    color: var(--color-1);\n}\n\n.stats-tabs li a em {\n    display: block;\n    margin: 0;\n    font-family: var(--font-1);\n    font-size: var(--text-sm);\n    line-height: var(--vspace-0_5);\n    font-weight: normal;\n    font-style: normal;\n    color: var(--color-text-light);\n}\n\n/* -------------------------------------------------------------------\n * ## skillbars\n * ------------------------------------------------------------------- */\n.skill-bars {\n    list-style: none;\n    margin: var(--vspace-2) 0 var(--vspace-1);\n}\n\n.skill-bars li {\n    height: .4rem;\n    background: var(--color-gray-3);\n    width: 100%;\n    margin-bottom: calc(var(--vspace-2) - .4rem);\n    padding: 0;\n    position: relative;\n}\n\n.skill-bars li strong {\n    position: absolute;\n    left: 0;\n    top: calc((var(--vspace-1) * 1.25) * -1);\n    font-family: var(--font-2);\n    font-weight: 500;\n    color: white;\n    text-transform: uppercase;\n    letter-spacing: .2em;\n    font-size: calc(var(--text-size) * 0.7778);\n    line-height: calc(0.75 * var(--space));\n}\n\n.skill-bars li .progress {\n    background: var(--color-black);\n    position: relative;\n    height: 100%;\n}\n\n.skill-bars li .progress span {\n    position: absolute;\n    right: 0;\n    top: calc((var(--vspace-1) + .8rem)* -1);\n    display: block;\n    font-family: var(--font-2);\n    color: white;\n    font-size: 1rem;\n    line-height: 1;\n    background: var(--color-black);\n    padding: calc(0.25 * var(--space)) calc(0.25 * var(--space));\n    border-radius: 4px;\n}\n\n.skill-bars li .progress span::after {\n    position: absolute;\n    left: 50%;\n    bottom: -10px;\n    margin-left: -5px;\n    width: 0;\n    height: 0;\n    border: 5px solid transparent;\n    border-top-color: var(--color-black, var(--color-black));\n    content: \"\";\n}\n\n.skill-bars li .percent5 {\n    width: 5%;\n}\n\n.skill-bars li .percent10 {\n    width: 10%;\n}\n\n.skill-bars li .percent15 {\n    width: 15%;\n}\n\n.skill-bars li .percent20 {\n    width: 20%;\n}\n\n.skill-bars li .percent25 {\n    width: 25%;\n}\n\n.skill-bars li .percent30 {\n    width: 30%;\n}\n\n.skill-bars li .percent35 {\n    width: 35%;\n}\n\n.skill-bars li .percent40 {\n    width: 40%;\n}\n\n.skill-bars li .percent45 {\n    width: 45%;\n}\n\n.skill-bars li .percent50 {\n    width: 50%;\n}\n\n.skill-bars li .percent55 {\n    width: 55%;\n}\n\n.skill-bars li .percent60 {\n    width: 60%;\n}\n\n.skill-bars li .percent65 {\n    width: 65%;\n}\n\n.skill-bars li .percent70 {\n    width: 70%;\n}\n\n.skill-bars li .percent75 {\n    width: 75%;\n}\n\n.skill-bars li .percent80 {\n    width: 80%;\n}\n\n.skill-bars li .percent85 {\n    width: 85%;\n}\n\n.skill-bars li .percent90 {\n    width: 90%;\n}\n\n.skill-bars li .percent95 {\n    width: 95%;\n}\n\n.skill-bars li .percent100 {\n    width: 100%;\n}\n\n/* -------------------------------------------------------------------\n * ## alert box\n * ------------------------------------------------------------------- */\n.alert-box {\n    padding: var(--vspace-0_75) 4rem var(--vspace-0_75) 3.2rem;\n    margin-bottom: var(--vspace-1);\n    border-radius: 4px;\n    font-family: var(--font-1);\n    font-weight: 500;\n    font-size: var(--text-sm);\n    line-height: var(--vspace-0_75);\n    opacity: 1;\n    visibility: visible;\n    position: relative;\n}\n\n.alert-box__close {\n    position: absolute;\n    display: block;\n    right: 1.6rem;\n    top: 1.6rem;\n    cursor: pointer;\n    width: 12px;\n    height: 12px;\n}\n\n.alert-box__close::before, .alert-box__close::after {\n    content: '';\n    position: absolute;\n    display: inline-block;\n    width: 2px;\n    height: 12px;\n    top: 0;\n    left: 5px;\n}\n\n.alert-box__close::before {\n    -webkit-transform: rotate(45deg);\n    transform: rotate(45deg);\n}\n\n.alert-box__close::after {\n    -webkit-transform: rotate(-45deg);\n    transform: rotate(-45deg);\n}\n\n.alert-box--error {\n    background-color: var(--color-error);\n    color: var(--color-error-content);\n}\n\n.alert-box--error .alert-box__close::before,\n.alert-box--error .alert-box__close::after {\n    background-color: var(--color-error-content);\n}\n\n.alert-box--success {\n    background-color: var(--color-success);\n    color: var(--color-success-content);\n}\n\n.alert-box--success .alert-box__close::before,\n.alert-box--success .alert-box__close::after {\n    background-color: var(--color-success-content);\n}\n\n.alert-box--info {\n    background-color: var(--color-info);\n    color: var(--color-info-content);\n}\n\n.alert-box--info .alert-box__close::before,\n.alert-box--info .alert-box__close::after {\n    background-color: var(--color-info-content);\n}\n\n.alert-box--notice {\n    background-color: var(--color-notice);\n    color: var(--color-notice-content);\n}\n\n.alert-box--notice .alert-box__close::before,\n.alert-box--notice .alert-box__close::after {\n    background-color: var(--color-notice-content);\n}\n\n.alert-box.hideit {\n    opacity: 0;\n    visibility: hidden;\n    -webkit-transition: all .5s;\n    transition: all .5s;\n}\n\n/* -------------------------------------------------------------------\n * ## pagination\n * ------------------------------------------------------------------- */\n.pgn {\n    --pgn-num-height: calc(var(--vspace-1) + .4rem);\n    margin: var(--vspace-1) auto var(--vspace-1);\n    text-align: center;\n}\n\n.pgn ul {\n    display: inline-block;\n    list-style: none;\n    margin-left: 0;\n    position: relative;\n    padding: 0 6rem;\n}\n\n.pgn ul li {\n    display: inline-block;\n    margin: 0;\n    padding: 0;\n}\n\n.pgn__num {\n    display: inline-block;\n    font-family: var(--font-2);\n    font-weight: 600;\n    font-size: var(--text-size);\n    line-height: var(--vspace-1);\n    padding: .2rem 1.2rem;\n    height: var(--pgn-num-height);\n    margin: .2rem .2rem;\n    color: var(--color-text-dark);\n    border-radius: 4px;\n    -webkit-transition: all, .3s, ease-in-out;\n    transition: all, .3s, ease-in-out;\n}\n\n.pgn__num:hover {\n    background: var(--color-gray-18);\n    color: var(--color-white);\n}\n\n.pgn .current, .pgn .current:hover {\n    background-color: var(--color-gray-18);\n    color: var(--color-white);\n}\n\n.pgn .inactive, .pgn .inactive:hover {\n    opacity: 0.4;\n    cursor: default;\n}\n\n.pgn__prev, .pgn__next {\n    display: -ms-inline-flexbox;\n    display: -webkit-inline-box;\n    display: inline-flex;\n    -ms-flex-flow: row wrap;\n    -webkit-box-orient: horizontal;\n    -webkit-box-direction: normal;\n    flex-flow: row wrap;\n    -ms-flex-pack: center;\n    -webkit-box-pack: center;\n    justify-content: center;\n    -ms-flex-align: center;\n    -webkit-box-align: center;\n    align-items: center;\n    height: var(--pgn-num-height);\n    width: 4.8rem;\n    line-height: var(--vspace-1);\n    border-radius: 4px;\n    padding: 0;\n    margin: 0;\n    opacity: 1;\n    font: 0/0 a;\n    text-shadow: none;\n    color: transparent;\n    -webkit-transition: all, .3s, ease-in-out;\n    transition: all, .3s, ease-in-out;\n    position: absolute;\n    top: 50%;\n    -webkit-transform: translate3d(0, -50%, 0);\n    transform: translate3d(0, -50%, 0);\n}\n\n.pgn__prev:hover, .pgn__next:hover {\n    background-color: var(--color-gray-18);\n}\n\n.pgn__prev svg, .pgn__next svg {\n    height: 2.4rem;\n    width: 2.4rem;\n    -webkit-transition: all, .3s, ease-in-out;\n    transition: all, .3s, ease-in-out;\n}\n\n.pgn__prev svg path, .pgn__next svg path {\n    fill: var(--color-text-dark);\n}\n\n.pgn__prev:hover svg path,\n.pgn__prev:focus svg path,\n.pgn__next:hover svg path,\n.pgn__next:focus svg path {\n    fill: white;\n}\n\n.pgn__prev {\n    left: 0;\n}\n\n.pgn__next {\n    right: 0;\n}\n\n.pgn__prev.inactive,\n.pgn__next.inactive {\n    opacity: 0.4;\n    cursor: default;\n}\n\n.pgn__prev.inactive:hover,\n.pgn__next.inactive:hover {\n    background-color: transparent;\n}\n\n/* -------------------------------------------------------------------\n * responsive:\n * pagination\n * ------------------------------------------------------------------- */\n@media screen and (max-width: 600px) {\n    .pgn ul {\n        padding: 0 5.2rem;\n    }\n}\n\n\n\n/* ===================================================================\n * # common and reusable styles\n *\n * ------------------------------------------------------------------- */\n.wide {\n    max-width: var(--width-wide);\n}\n\n.wider {\n    max-width: var(--width-wider);\n}\n\n.narrow {\n    max-width: var(--width-narrow);\n}\n\n.narrower {\n    max-width: var(--width-narrower);\n}\n\n/* -------------------------------------------------------------------\n * ## animation stuff\n * ------------------------------------------------------------------- */\n.animate-this {\n    opacity: 0;\n    visibility: hidden;\n}\n\n.no-js .animate-this, .no-cssanimations .animate-this {\n    opacity: 1;\n    visibility: visible;\n}\n\n.animated {\n    -webkit-animation-duration: 1s;\n    animation-duration: 1s;\n    -webkit-animation-fill-mode: both;\n    animation-fill-mode: both;\n    -webkit-animation-name: fadeInUp;\n    animation-name: fadeInUp;\n}\n\n/* fade in */\n@-webkit-keyframes fadeInUp {\n    from {\n        opacity: 0;\n        visibility: hidden;\n        -webkit-transform: translate3d(0, 150%, 0);\n        transform: translate3d(0, 150%, 0);\n    }\n    to {\n        opacity: 1;\n        visibility: visible;\n        -webkit-transform: translate3d(0, 0, 0);\n        transform: translate3d(0, 0, 0);\n    }\n}\n\n@keyframes fadeInUp {\n    from {\n        opacity: 0;\n        visibility: hidden;\n        -webkit-transform: translate3d(0, 150%, 0);\n        transform: translate3d(0, 150%, 0);\n    }\n    to {\n        opacity: 1;\n        visibility: visible;\n        -webkit-transform: translate3d(0, 0, 0);\n        transform: translate3d(0, 0, 0);\n    }\n}\n\n\n/* -------------------------------------------------------------------\n * ## slick slider\n * ------------------------------------------------------------------- */\n.slick-slider .slick-slide {\n    outline: none;\n}\n\n.slick-slider .slick-dots {\n    display: block;\n    list-style: none;\n    width: 100%;\n    padding: 0;\n    margin: var(--vspace-1) 0 0 0;\n    text-align: center;\n    position: absolute;\n    top: 100%;\n    left: 0;\n}\n\n.slick-slider .slick-dots li {\n    display: inline-block;\n    width: 20px;\n    height: 20px;\n    margin: 0;\n    padding: 5px;\n    cursor: pointer;\n}\n\n.slick-slider .slick-dots li button {\n    display: block;\n    width: 10px;\n    height: 10px;\n    line-height: 10px;\n    border-radius: 50%;\n    background: rgba(255, 255, 255, 0.6);\n    border: none;\n    padding: 0;\n    margin: 0;\n    cursor: pointer;\n    font: 0/0 a;\n    text-shadow: none;\n    color: transparent;\n}\n\n.slick-slider .slick-dots li button:hover,\n.slick-slider .slick-dots li button:focus {\n    outline: none;\n}\n\n.slick-slider .slick-dots li.slick-active button,\n.slick-slider .slick-dots li:hover button {\n    background: white;\n}\n\n/* -------------------------------------------------------------------\n * ## masonry entries\n * ------------------------------------------------------------------- */\n.bricks-wrapper .entry {\n    margin-bottom: 4rem;\n}\n\n.bricks-wrapper .entry__thumb,\n.bricks-wrapper .entry__text {\n    -webkit-box-shadow: 0 2px 3px rgba(0, 0, 0, 0.05);\n    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.05);\n}\n\n.bricks-wrapper .entry__thumb {\n    overflow: hidden;\n    position: relative;\n}\n\n.bricks-wrapper .entry__thumb img {\n    vertical-align: bottom;\n    -webkit-transition: all 0.3s ease-in-out;\n    transition: all 0.3s ease-in-out;\n    margin: 0;\n}\n\n.bricks-wrapper .entry__thumb .thumb-link::before {\n    z-index: 1;\n    content: \"\";\n    display: block;\n    background: rgba(0, 0, 0, 0.8);\n    opacity: 0;\n    visibility: hidden;\n    width: 100%;\n    height: 100%;\n    -webkit-transition: all 0.3s ease-in-out;\n    transition: all 0.3s ease-in-out;\n    position: absolute;\n    top: 0;\n    left: 0;\n}\n\n.bricks-wrapper .entry__thumb .thumb-link::after {\n    z-index: 1;\n    display: block;\n    content: \"...\";\n    font-family: georgia, serif;\n    font-size: 3.2rem;\n    height: 90px;\n    width: 90px;\n    letter-spacing: .2rem;\n    line-height: 90px;\n    margin-left: -45px;\n    margin-top: -45px;\n    text-align: center;\n    color: white;\n    opacity: 0;\n    visibility: hidden;\n    -webkit-transform: scale(0.5);\n    transform: scale(0.5);\n    -webkit-transition: all 0.3s ease-in-out;\n    transition: all 0.3s ease-in-out;\n    position: absolute;\n    left: 50%;\n    top: 50%;\n}\n\n.bricks-wrapper .entry__thumb:hover .thumb-link::before {\n    opacity: 1;\n    visibility: visible;\n}\n\n.bricks-wrapper .entry__thumb:hover .thumb-link::after {\n    opacity: 1;\n    visibility: visible;\n    -webkit-transform: scale(1);\n    transform: scale(1);\n}\n\n.bricks-wrapper .entry__thumb:hover .thumb-link img {\n    -webkit-transform: scale(1.05);\n    transform: scale(1.05);\n}\n\n.bricks-wrapper .entry__text {\n    padding: 2.4rem 2.8rem 3.6rem;\n    background-color: white;\n}\n\n.bricks-wrapper .entry__title {\n    font-size: var(--text-lg);\n    font-weight: 500;\n    line-height: var(--vspace-1);\n    margin-top: 0;\n    margin-bottom: var(--vspace-0_5);\n}\n\n.bricks-wrapper .entry__title a,\n.bricks-wrapper .entry__title a:visited {\n    color: black;\n}\n\n.bricks-wrapper .entry__meta {\n    font-family: var(--font-1);\n    font-size: var(--text-sm);\n    margin-bottom: calc(0.125 * var(--space));\n}\n\n.bricks-wrapper .entry__meta a,\n.bricks-wrapper .entry__meta a:visited {\n    color: black;\n}\n\n.bricks-wrapper .entry__cat-links a::after {\n    content: \", \";\n}\n\n.bricks-wrapper .entry__cat-links a:last-child::after {\n    display: none;\n}\n\n.bricks-wrapper .entry__excerpt {\n    font-size: 1.5rem;\n    line-height: 1.733;\n    color: var(--color-text-light);\n}\n\n.bricks-wrapper .entry__excerpt p {\n    margin-bottom: 0;\n}\n\n/* featured post slider\n * ------------------------------------------------------------------- */\n.featured-grid .entry__content {\n    position: relative;\n}\n\n.featured-grid .entry__content:hover .f-slide__overlay {\n    background-color: rgba(0, 0, 0, 0.5);\n}\n\n.f-slide {\n    display: -webkit-box;\n    display: -ms-flexbox;\n    display: flex;\n    -ms-flex-align: center;\n    -webkit-box-align: center;\n    align-items: center;\n    -ms-flex-pack: center;\n    -webkit-box-pack: center;\n    justify-content: center;\n    height: 600px;\n    width: 100%;\n    background-color: white;\n    padding: var(--vspace-2) 15% var(--vspace-1);\n    text-align: center;\n    position: relative;\n}\n\n.f-slide__background {\n    background-size: cover;\n    background-position: center;\n    background-repeat: no-repeat;\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    width: 100%;\n    height: 100%;\n}\n\n.f-slide__overlay {\n    background-color: rgba(0, 0, 0, 0.3);\n    -webkit-transition: all 0.3s ease-in-out;\n    transition: all 0.3s ease-in-out;\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    width: 100%;\n    height: 100%;\n}\n\n.f-slide__content {\n    position: relative;\n}\n\n.f-slide__meta {\n    font-family: var(--font-1);\n    font-size: set-text-size(0.8888);\n    margin: -3.2rem 0 0.8rem 0;\n    color: rgba(255, 255, 255, 0.6);\n    line-height: 1.5;\n}\n\n.f-slide__meta li {\n    display: inline-block;\n    margin: 0 3px;\n    padding: 0;\n}\n\n.f-slide__meta li a {\n    color: rgba(255, 255, 255, 0.6);\n}\n\n.f-slide__meta li a:hover,\n.f-slide__meta li a:focus {\n    color: white;\n    border-color: rgba(255, 255, 255, 0.2);\n}\n\n.f-slide__title {\n    font-family: var(--font-2);\n    font-weight: 500;\n    font-size: var(--text-xxl);\n    line-height: 1.286;\n    margin-top: 0;\n    margin-bottom: var(--vspace-1);\n    color: white;\n}\n\n.f-slide__title a {\n    color: white;\n    border-bottom: 1px solid transparent;\n}\n\n.f-slide__title a:hover, .f-slide__title a:focus {\n    color: white;\n    border-color: rgba(255, 255, 255, 0.1);\n}\n\n/* featured-post-slider direction nav\n * --------------------------------------------------------- */\n.featured-post-nav button {\n    z-index: 2;\n    background-color: black;\n    border: black;\n    padding: 0;\n    margin: 0;\n    height: 6rem;\n    width: 6rem;\n    cursor: pointer;\n    display: -ms-flexbox;\n    display: -webkit-box;\n    display: flex;\n    -ms-flex-align: center;\n    -webkit-box-align: center;\n    align-items: center;\n    -ms-flex-pack: center;\n    -webkit-box-pack: center;\n    justify-content: center;\n    -webkit-transform: translateY(-50%);\n    transform: translateY(-50%);\n    position: absolute;\n    top: 50%;\n}\n\n.featured-post-nav button svg {\n    height: 2rem;\n    width: 2rem;\n}\n\n.featured-post-nav button svg path {\n    fill: rgba(255, 255, 255, 0.5);\n    -webkit-transition: all .3s;\n    transition: all .3s;\n}\n\n.featured-post-nav button:hover svg path,\n.featured-post-nav button:focus svg path {\n    fill: white;\n}\n\n.featured-post-nav__prev {\n    left: 0;\n}\n\n.featured-post-nav__next {\n    right: 0;\n}\n\n/*  post formats - masonry view\n * ------------------------------------------------------------------- */\n\n/* format quote & format link\n * --------------------------------------- */\n.format-quote, .format-link {\n    text-align: center;\n}\n\n.format-quote .entry__thumb,\n.format-link .entry__thumb {\n    display: table;\n    background: white;\n    padding: 2.4rem 3rem 4.8rem;\n    min-height: 320px;\n    width: 100%;\n    position: relative;\n}\n\n.format-quote .entry__thumb::before,\n.format-link .entry__thumb::before {\n    content: \"\";\n    display: block;\n    height: 3.6rem;\n    width: 3.6rem;\n    background-position: center center;\n    background-repeat: no-repeat;\n    background-size: contain;\n    margin-bottom: 1.5rem;\n    position: absolute;\n    top: var(--vspace-1_25);\n    left: 50%;\n    -webkit-transform: translateX(-50%);\n    transform: translateX(-50%);\n}\n\n.format-quote blockquote::before {\n    display: none;\n}\n\n.format-quote blockquote,\n.format-link .link-wrap {\n    display: table-cell;\n    margin: 0;\n    padding: 0;\n    vertical-align: middle;\n    border: none;\n}\n\n.format-quote blockquote p,\n.format-link .link-wrap p {\n    font-family: var(--font-2);\n    font-weight: 500;\n    font-size: var(--text-lg);\n    line-height: var(--vspace-1);\n    color: black;\n    padding: 7.8rem 0 0 0;\n    margin-bottom: var(--vspace-1);\n}\n\n.format-quote cite, .format-link cite {\n    display: block;\n    font-family: var(--font-1);\n    font-size: 1.5rem;\n    font-style: normal;\n    line-height: 1.6;\n    color: var(--color-text-light);\n}\n\n.format-quote cite::before,\n.format-link cite::before {\n    display: none;\n}\n\n.format-quote cite a,\n.format-quote cite a:visited {\n    color: var(--color-text-light);\n    border: none;\n}\n\n.format-quote .entry__thumb::before {\n    /*background-image: url(../images/icons/icon-quote.svg);*/\n}\n\n.format-link .entry__thumb::before {\n    /*background-image: url(../images/icons/icon-link.svg);*/\n    width: 3.2rem;\n    height: 3.2rem;\n    background-repeat: no-repeat;\n    background-position: center;\n    background-size: 3rem 3rem;\n}\n\n.format-link .link-wrap cite:before {\n    display: none;\n}\n\n.format-link .link-wrap cite a,\n.format-link .link-wrap cite a:visited {\n    color: var(--color-text-light);\n    display: inline-block;\n    padding-bottom: .4rem;\n}\n\n.format-link .link-wrap cite a:hover,\n.format-link .link-wrap cite a:focus {\n    color: black;\n}\n\n/* format video & format audio\n * --------------------------------------- */\n.format-video .entry__thumb::after,\n.format-audio .entry__thumb::after {\n    content: \"\";\n    display: block;\n    height: 5.6rem;\n    width: 5.6rem;\n    background-position: center center;\n    background-repeat: no-repeat;\n    background-color: rgba(0, 0, 0, 0.3);\n    border-radius: 50%;\n    position: absolute;\n    top: 2.4rem;\n    left: 2.4rem;\n}\n\n.format-video .thumb-link::before,\n.format-video .thumb-link::after,\n.format-audio .thumb-link::before,\n.format-audio .thumb-link::after {\n    display: none !important;\n}\n\n.format-video .entry__thumb::after {\n    cursor: -webkit-zoom-in;\n    cursor: zoom-in;\n    /*background-image: url(../images/icons/icon-video.svg);*/\n    background-size: 2.8rem 2.8rem;\n}\n\n.format-video .entry__thumb-link {\n    cursor: -webkit-zoom-in;\n    cursor: zoom-in;\n}\n\n.format-audio .entry__thumb::after {\n    /*background-image: url(../images/icons/icon-audio.svg);*/\n    background-size: 2.8rem 2.8rem;\n}\n\n/* -------------------------------------------------------------------\n * ## format gallery\n * ------------------------------------------------------------------- */\n.format-gallery .entry__thumb {\n    overflow: hidden;\n}\n\n.format-gallery .slider {\n    overflow: hidden;\n}\n\n.format-gallery .slick-dots {\n    text-align: right;\n    margin: 0;\n    padding: 0 2.4rem 0 2rem;\n    position: absolute;\n    top: auto;\n    bottom: var(--vspace-0_75);\n    right: 0;\n}\n\n.format-gallery .slider__slides {\n    cursor: pointer;\n    overflow: hidden;\n    opacity: 0;\n    visibility: hidden;\n}\n\n.format-gallery .slider__slides.slick-initialized {\n    opacity: 1;\n    visibility: visible;\n}\n\n/* responsive:\n/* common and reusable styles\n * ------------------------------------------------------------------- */\n@media screen and (max-width: 1400px) {\n    .bricks-wrapper .entry {\n        margin-bottom: 3.6rem;\n    }\n}\n\n@media screen and (max-width: 1040px) {\n    .bricks-wrapper .entry__title,\n    .format-quote blockquote p,\n    .format-link .link-wrap p {\n        font-size: var(--text-md);\n        line-height: calc(0.875 * var(--space));\n    }\n}\n\n@media screen and (max-width: 980px) {\n    .bricks-wrapper .entry__title,\n    .format-quote blockquote p,\n    .format-link .link-wrap p {\n        font-size: var(--text-lg);\n        line-height: var(--vspace-1);\n    }\n}\n\n@media screen and (max-width: 800px) {\n    .bricks-wrapper .entry {\n        margin-bottom: 3.2rem;\n    }\n}\n\n@media screen and (max-width: 700px) {\n    .bricks-wrapper .entry {\n        margin-bottom: 2.4rem;\n    }\n    .bricks-wrapper .entry__title,\n    .format-quote blockquote p,\n    .format-link .link-wrap p {\n        font-size: var(--text-md);\n        line-height: calc(0.875 * var(--space));\n    }\n}\n\n@media screen and (max-width: 600px) {\n    .bricks-wrapper .entry {\n        margin-bottom: 4rem;\n    }\n    .bricks-wrapper .entry__title,\n    .format-quote blockquote p,\n    .format-link .link-wrap p {\n        font-size: var(--text-lg);\n        line-height: var(--vspace-1);\n    }\n    .f-slide {\n        height: 552px;\n    }\n    .f-slide__meta {\n        font-size: var(--text-sm);\n    }\n    .f-slide__title {\n        font-size: var(--text-xl);\n    }\n    .featured-post-nav button {\n        height: 5.2rem;\n        width: 5.2rem;\n        -webkit-transform: translateY(0);\n        transform: translateY(0);\n        top: auto;\n        bottom: 0;\n    }\n    .featured-post-nav button svg {\n        height: 1.6rem;\n        width: 1.6rem;\n    }\n}\n\n@media screen and (max-width: 400px) {\n    .bricks-wrapper .entry {\n        margin-bottom: 3.6rem;\n    }\n    .f-slide {\n        height: 440px;\n    }\n    .featured-post-nav button {\n        height: 4.4rem;\n        width: 4.4rem;\n    }\n    .featured-post-nav button svg {\n        height: 1.4rem;\n        width: 1.4rem;\n    }\n}\n\n\n\n\n\n\n\n/* ===================================================================\n * # pageheader\n *\n * ------------------------------------------------------------------- */\n.s-pageheader {\n    padding: var(--vspace-1_5) 0 0 0;\n    text-align: center;\n}\n\n.s-pageheader .row {\n    max-width: 900px;\n}\n\n.s-pageheader h1 {\n    margin-top: 0;\n    margin-bottom: var(--vspace-1);\n}\n\n\n\n/* ===================================================================\n * # content wrap\n *\n * ------------------------------------------------------------------- */\n.s-content {\n    --row-max-width: 1080px;\n    padding-top: var(--topspace);\n    padding-bottom: var(--vspace-0_125);\n}\n\n.s-content--single {\n    padding-bottom: var(--vspace-0_125);\n}\n\n.s-content .row {\n    max-width: var(--row-max-width);\n}\n\n.s-content .row.wide {\n    max-width: 1400px;\n}\n\n.s-content .row.narrow {\n    max-width: 800px;\n}\n\n.s-content__media {\n    position: relative;\n    margin-bottom: var(--vspace-2_5);\n}\n\n.s-content__media img {\n    vertical-align: bottom;\n    margin-bottom: 0;\n}\n\n.s-content__primary {\n    padding-left: 20rem;\n    padding-right: 20rem;\n}\n\n.s-content__primary img {\n    max-width: calc(var(--row-max-width) - var(--gutter-lg) * 2);\n    margin: var(--vspace-1) 0 var(--vspace-1) -8rem;\n}\n\n.s-content__title {\n    text-align: center;\n}\n\n.s-content__title--post {\n    margin-bottom: var(--vspace-0_25);\n}\n\n.s-content__blocks h4 {\n    margin-top: 0;\n}\n\n.s-content__form {\n    margin-top: var(--vspace-2);\n}\n\n/* responsive:\n * content wrap\n * ------------------------------------------------------------------- */\n@media screen and (max-width: 1200px) {\n    .s-content {\n        --row-max-width: 1000px;\n    }\n    .s-content__primary {\n        padding-left: 4.8rem;\n        padding-right: 4.8rem;\n    }\n    .s-content__primary img {\n        max-width: calc(var(--row-max-width) - var(--gutter-md) * 2);\n        margin: var(--vspace-1) 0 var(--vspace-1) -4.8rem;\n    }\n}\n\n@media screen and (max-width: 1100px) {\n    .s-content {\n        --row-max-width: 920px;\n    }\n    .s-content__primary {\n        padding-left: 4rem;\n        padding-right: 4rem;\n    }\n    .s-content__primary img {\n        margin: var(--vspace-1) 0 var(--vspace-1) -4rem;\n    }\n}\n\n@media screen and (max-width: 1020px) {\n    .s-content__primary {\n        padding-left: 3.2rem;\n        padding-right: 3.2rem;\n    }\n    .s-content__primary img {\n        max-width: 100%;\n        margin: var(--vspace-1) 0;\n    }\n}\n\n@media screen and (max-width: 800px) {\n    .s-content {\n        padding-top: var(--topspace);\n    }\n    .s-content__primary {\n        padding-left: 0;\n        padding-right: 0;\n    }\n    .s-content__media {\n        margin-bottom: var(--vspace-2);\n    }\n}\n\n@media screen and (max-width: 600px) {\n    .s-content {\n        padding-top: var(--topspace);\n    }\n    .s-content__media {\n        margin-bottom: var(--vspace-1_5);\n    }\n}\n\n@media screen and (max-width: 400px) {\n    .s-content__title {\n        font-size: var(--text-xxl);\n        line-height: var(--vspace-1_5);\n    }\n}\n\n\n\n/* ===================================================================\n * # bricks masonry\n *\n * ------------------------------------------------------------------- */\n.s-bricks {\n    padding-top: 4.2rem;\n}\n\n.s-bricks.with-top-sep {\n    position: relative;\n}\n\n.s-bricks.with-top-sep::before {\n    content: \"\";\n    display: block;\n    position: absolute;\n    left: 50%;\n    top: 0;\n    margin-left: -100px;\n    width: 200px;\n    height: 1px;\n    background-color: var(--color-border);\n}\n\n.s-bricks .masonry {\n    max-width: 1440px;\n    width: 94%;\n    margin: 0 auto var(--vspace-1);\n}\n\n.s-bricks .pagination {\n    margin-top: 6rem;\n}\n\n.bricks-wrapper .grid-sizer,\n.bricks-wrapper .brick {\n    width: 100%;\n}\n\n.bricks-wrapper .brick {\n    padding: 0;\n}\n\n.bricks-wrapper .featured-grid {\n    width: 100%;\n}\n\n.bricks-wrapper .featured-grid .entry-content {\n    width: 100%;\n    background: #151515;\n}\n\n/* responsive:\n/* bricks masonry\n * ------------------------------------------------------------------- */\n@media screen and (max-width: 1440px) {\n    .s-bricks .masonry {\n        width: 98%;\n    }\n}\n\n@media screen and (max-width: 1400px) {\n    .s-bricks .masonry {\n        max-width: var(--width-max);\n        width: 94%;\n    }\n    .bricks-wrapper .grid-sizer,\n    .bricks-wrapper .brick {\n        width: 100%;\n    }\n    .bricks-wrapper .brick {\n        padding: 0;\n    }\n    .bricks-wrapper .featured-grid {\n        width: 100%;\n    }\n}\n\n@media screen and (max-width: 980px) {\n    .s-bricks .masonry {\n        max-width: 780px;\n        width: 96%;\n    }\n    .bricks-wrapper .grid-sizer,\n    .bricks-wrapper .brick {\n        width: 100%;\n    }\n    .bricks-wrapper .featured-grid {\n        width: 100%;\n    }\n}\n\n@media screen and (max-width: 800px) {\n    .bricks-wrapper .brick {\n        padding: 0;\n    }\n}\n\n@media screen and (max-width: 700px) {\n    .bricks-wrapper .brick {\n        padding: 0;\n    }\n}\n\n@media screen and (max-width: 600px) {\n    .s-bricks .masonry {\n        max-width: 480px;\n        width: 100%;\n        padding-left: 6vw;\n        padding-right: 6vw;\n    }\n    .bricks-wrapper .grid-sizer,\n    .bricks-wrapper .brick {\n        float: none;\n        width: 100%;\n        padding: 0 var(--gutter-mob);\n        clear: both;\n    }\n}\n\n@media screen and (max-width: 400px) {\n    .bricks-wrapper .brick {\n        margin-left: 0;\n        margin-right: 0;\n        padding: 0;\n    }\n}\n\n\n\n/* ===================================================================\n * # footer\n *\n * ------------------------------------------------------------------- */\n.s-footer {\n    padding-top: var(--vspace-2_5);\n    padding-bottom: var(--vspace-2);\n    font-size: 1.6rem;\n    line-height: 2.8rem;\n    color: var(--color-text-light);\n}\n\n.s-footer .row {\n    max-width: 1240px;\n}\n\n.s-footer [class*=\"column\"] {\n    margin-bottom: var(--vspace-0_5);\n}\n\n.s-footer a {\n    color: black;\n}\n\n.s-footer h5 {\n    font-size: var(--text-size);\n    font-weight: 500;\n    padding-bottom: var(--vspace-0_75);\n    margin-bottom: var(--vspace-0_75);\n    margin-top: 0;\n    border-bottom: 1px solid var(--color-border);\n}\n\n.s-footer ul {\n    margin-left: 0;\n}\n\n.s-footer li {\n    padding-left: 0;\n}\n\n/* -------------------------------------------------------------------\n * ## footer main\n * ------------------------------------------------------------------- */\n.s-footer__main {\n    padding-bottom: var(--vspace-0_5);\n}\n\n.s-footer__main ul li {\n    list-style: none;\n}\n\n.s-footer__main ul a {\n    color: var(--color-text-light);\n}\n\n.s-footer__main ul a:hover, .s-footer__main ul a:focus {\n    color: black;\n}\n\n/* footer subscribe\n * -------------------------------------- */\n.s-footer__subscribe p {\n    margin-bottom: var(--vspace-0_5);\n}\n\n.s-footer__subscribe .subscribe-form #mc-form {\n    padding: 0;\n}\n\n.s-footer__subscribe .subscribe-form input[type=\"email\"] {\n    padding: .8rem 2rem;\n    border: none;\n    width: 100%;\n    margin-bottom: var(--vspace-0_5);\n}\n\n.s-footer__subscribe .subscribe-form input[type=\"submit\"] {\n    display: none;\n}\n\n.s-footer__subscribe .subscribe-form .subscribe-message {\n    font-family: var(--font-1);\n    font-size: var(--text-sm);\n    color: black;\n}\n\n.s-footer__subscribe .subscribe-form .subscribe-message i {\n    color: black;\n    margin-right: .4rem;\n}\n\n/* -------------------------------------------------------------------\n * ## footer bottom\n * ------------------------------------------------------------------- */\n.s-footer__bottom {\n    margin-top: var(--vspace-0_5);\n    text-align: center;\n}\n\n/* copyright\n * -------------------------------------- */\n.ss-copyright span {\n    display: inline-block;\n    font-size: var(--text-sm);\n    line-height: 2.4rem;\n}\n\n.ss-copyright span::after {\n    content: \"|\";\n    display: inline-block;\n    padding: 0 1rem 0 1.2rem;\n    color: rgba(0, 0, 0, 0.2);\n}\n\n.ss-copyright span:last-child::after {\n    display: none;\n}\n\n/* -------------------------------------------------------------------\n * ## go top\n * ------------------------------------------------------------------- */\n.ss-go-top {\n    z-index: 2;\n    opacity: 0;\n    visibility: hidden;\n    -webkit-transform: translate3d(0, 200%, 0);\n    transform: translate3d(0, 200%, 0);\n    -webkit-transition: all 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);\n    transition: all 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);\n    position: fixed;\n    bottom: 6.4rem;\n    right: 6.4rem;\n}\n\n.ss-go-top a {\n    display: -ms-flexbox;\n    display: -webkit-box;\n    display: flex;\n    -ms-flex-align: center;\n    -webkit-box-align: center;\n    align-items: center;\n    -ms-flex-pack: center;\n    -webkit-box-pack: center;\n    justify-content: center;\n    text-decoration: none;\n    border: none;\n    height: 6.4rem;\n    width: 6.4rem;\n    border-radius: 50%;\n    background-color: var(--color-gray-3);\n    -webkit-transition: all .3s;\n    transition: all .3s;\n    position: relative;\n}\n\n.ss-go-top a:hover, .ss-go-top a:focus {\n    background-color: black;\n}\n\n.ss-go-top a:hover svg path, .ss-go-top a:focus svg path {\n    fill: white;\n}\n\n.ss-go-top svg {\n    height: 2.4rem;\n    width: 2.4rem;\n}\n\n.ss-go-top svg path {\n    fill: black;\n}\n\n.ss-go-top.link-is-visible {\n    opacity: 1;\n    visibility: visible;\n    -webkit-transform: translate3d(0, 0, 0);\n    transform: translate3d(0, 0, 0);\n}\n\n/* responsive:\n * footer\n * ------------------------------------------------------------------- */\n@media screen and (max-width: 800px) {\n    .s-footer {\n        padding-bottom: var(--vspace-1_5);\n    }\n    .ss-go-top {\n        right: 3.6rem;\n        bottom: 4.8rem;\n    }\n    .ss-go-top a {\n        height: 5.2rem;\n        width: 5.2rem;\n    }\n    .ss-go-top svg {\n        height: 2rem;\n        width: 2rem;\n    }\n}\n\n@media screen and (max-width: 600px) {\n    .s-footer__bottom {\n        padding-bottom: .8rem;\n    }\n    .ss-copyright span {\n        display: block;\n    }\n    .ss-copyright span::after {\n        display: none;\n    }\n    .ss-go-top {\n        right: 2.4rem;\n        bottom: 4rem;\n    }\n}\n\n\n\n/* ===================================================================\n * # blog styles and blog components\n *\n * ------------------------------------------------------------------- */\n\n/* -------------------------------------------------------------------\n * ## single post meta\n * ------------------------------------------------------------------- */\n.s-content__post-meta {\n    font-family: var(--font-1);\n    font-size: calc(var(--text-size) * 0.944);\n    list-style: none;\n    text-align: center;\n    margin-left: 0;\n    margin-bottom: var(--vspace-1_75);\n    color: var(--color-text-light);\n}\n\n.s-content__post-meta a {\n    color: black;\n}\n\n.s-content__post-meta li {\n    display: inline-block;\n    padding-left: 0;\n    margin: 0 .8rem;\n}\n\n.s-content__post-meta .cat a {\n    margin-right: .6rem;\n}\n\n.s-content__post-meta .cat a:last-child {\n    margin-right: 0;\n}\n\n.s-content__post-meta .cat a:last-child::after {\n    content: none;\n}\n\n.s-content__post-meta .cat a::after {\n    content: \",\";\n}\n\n/* -------------------------------------------------------------------\n * ## single post tags\n * ------------------------------------------------------------------- */\n.s-content__post-tags {\n    margin: var(--vspace-2) 0 var(--vspace-1_25);\n}\n\n.s-content__post-tags span {\n    color: black;\n    margin-right: 1rem;\n    font-weight: 600;\n}\n\n.s-content__post-tags a {\n    color: var(--color-text-light);\n    margin-right: 1rem;\n}\n\n/* -------------------------------------------------------------------\n * ## author profile\n * ------------------------------------------------------------------- */\n.s-content__author {\n    margin-top: var(--vspace-2_5);\n    padding-top: var(--vspace-0_25);\n    padding-left: 9.6rem;\n    position: relative;\n}\n\n.s-content__author a {\n    color: black;\n}\n\n.s-content__author h5 {\n    margin-top: 0;\n}\n\n.s-content__author img {\n    margin: .6rem 0 0 0 !important;\n    width: 6.4rem;\n    height: 6.4rem;\n    border-radius: 50%;\n    position: absolute;\n    left: 0;\n    top: 0;\n}\n\n.s-content__author p {\n    margin-bottom: var(--vspace-0_5);\n}\n\n.s-content__author .author-social {\n    list-style: none;\n    margin-left: 0;\n    font-size: var(--text-xs);\n    text-transform: uppercase;\n    letter-spacing: .2em;\n}\n\n.s-content__author .author-social li {\n    display: inline-block;\n    margin-right: 0.8rem;\n    padding-left: 0;\n}\n\n.s-content__author .author-social a {\n    color: var(--color-text-light);\n}\n\n/* -------------------------------------------------------------------\n * ## single post page nav\n * ------------------------------------------------------------------- */\n.s-content__pagenav {\n    padding-top: var(--vspace-0_5);\n    margin: 12rem 0;\n    border-top: 1px solid var(--color-border);\n}\n\n.s-content__pagenav div {\n    float: left;\n    width: 50%;\n    padding-top: var(--vspace-0_5);\n    padding-right: var(--gutter-lg);\n}\n\n.s-content__pagenav a {\n    font-family: var(--font-2);\n    font-weight: 600;\n    font-size: var(--text-lg);\n    line-height: var(--vspace-1);\n    border: none;\n    color: black;\n}\n\n.s-content__pagenav a span {\n    display: block;\n    font-family: var(--font-1);\n    font-weight: 400;\n    font-size: var(--text-sm);\n    line-height: var(--vspace-1);\n    margin-bottom: var(--vspace-0_5);\n    color: var(--color-text-light);\n    -webkit-transition: all 0.3s ease-in-out;\n    transition: all 0.3s ease-in-out;\n}\n\n.s-content__pagenav a:hover span {\n    color: black;\n}\n\n/* -------------------------------------------------------------------\n * responsive:\n * blog styles and blog components\n * ------------------------------------------------------------------- */\n@media screen and (max-width: 800px) {\n    .s-content__author {\n        padding-left: 0;\n        text-align: center;\n    }\n    .s-content__author img {\n        position: static;\n        width: 6.6rem;\n        height: 6.6rem;\n        margin: 0 0 0.8rem 0 !important;\n    }\n}\n\n@media screen and (max-width: 640px) {\n    .s-content__pagenav {\n        text-align: center;\n    }\n    .s-content__pagenav div {\n        float: none;\n        width: 100%;\n        padding-right: 0;\n    }\n    .s-content__pagenav div:first-child {\n        margin-bottom: var(--vspace-0_5);\n    }\n}\n\n\n/* -------------------------------------------------------------------\n * ## comments\n * ------------------------------------------------------------------- */\n.comments-wrap {\n    margin-top: var(--vspace-3);\n    padding: var(--vspace-3_5) 0 var(--vspace-3_5);\n    background-color: var(--color-gray-1);\n}\n\n.comments-wrap .column {\n    padding: 0 calc(8rem + var(--gutter-lg));\n}\n\n.comments-wrap h3 {\n    margin-top: 0;\n    margin-bottom: var(--vspace-0_5);\n}\n\n.comments-wrap h3 span {\n    display: block;\n    font-family: var(--font-1);\n    font-weight: 400;\n    font-size: calc(var(--text-size) * 0.8889);\n    line-height: var(--vspace-0_75);\n    color: var(--color-text-light);\n    margin-top: calc(0.125 * var(--space));\n}\n\n/* comments\n * -------------------------------------------------- */\n#comments {\n    padding-top: var(--vspace-0_5);\n    padding-bottom: var(--vspace-0_5);\n}\n\n#comments ol, #comments ul {\n    list-style: none;\n}\n\n.commentlist {\n    margin: var(--vspace-2) 0 var(--vspace-1_5);\n    padding: 0;\n}\n\n.commentlist>.comment {\n    position: relative;\n    list-style: none;\n    margin: 0;\n    padding: 2rem 0 0 0;\n    padding-left: 14%;\n}\n\n.comment__avatar {\n    position: absolute;\n    left: 0;\n    display: block;\n}\n\n.comment__avatar img {\n    height: var(--vspace-2);\n    width: var(--vspace-2);\n    border-radius: 100%;\n    vertical-align: bottom;\n}\n\n.comment__info {\n    position: relative;\n}\n\n.comment__info .comment__author {\n    font-weight: 600;\n    font-size: var(--text-size);\n    line-height: var(--vspace-1);\n    font-style: normal;\n    color: black;\n}\n\n.comment__meta {\n    display: block;\n    font-style: normal;\n    line-height: var(--vspace-0_75);\n    color: var(--color-text-light);\n    text-align: right;\n    width: 150px;\n    position: absolute;\n    top: 0;\n    right: 0;\n}\n\n.comment__meta .comment__time {\n    font-size: calc(var(--text-size) * 0.7778);\n    text-transform: uppercase;\n    letter-spacing: .1rem;\n    color: var(--color-text-light);\n}\n\n.comment__meta .comment__reply a {\n    font-size: var(--text-xs);\n    font-family: var(--font-1);\n    font-weight: 600;\n    text-transform: uppercase;\n    letter-spacing: .2rem;\n    color: black;\n}\n\n.comment__text {\n    clear: both;\n    margin: var(--vspace-0_5) 0 0 0;\n    padding: 0 175px 0 0;\n}\n\n.comment ul.children {\n    margin: 0;\n    padding: 0;\n}\n\n.comment ul.children li {\n    padding-left: 5%;\n    margin-top: calc(var(--vspace-1) + 2rem);\n    border-left: 1px solid var(--color-border);\n}\n\n/* comments form\n * ---------------------------- */\n.comments-wrap .comment-respond {\n    margin-top: var(--vspace-2);\n}\n\n.comment-respond form {\n    padding-top: var(--vspace-1_75);\n}\n\n/* -------------------------------------------------------------------\n * responsive:\n * comments\n * ------------------------------------------------------------------- */\n@media screen and (max-width: 1200px) {\n    .comments-wrap .column {\n        padding: 0 calc(4.8rem + var(--gutter-md));\n    }\n}\n\n@media screen and (max-width: 1100px) {\n    .comments-wrap .column {\n        padding: 0 calc(4rem + var(--gutter-md));\n    }\n}\n\n@media screen and (max-width: 1020px) {\n    .comments-wrap .column {\n        padding: 0 calc(3.2rem + var(--gutter-md));\n    }\n}\n\n@media screen and (max-width: 1000px) {\n    .comment__avatar img {\n        height: var(--vspace-1_75);\n        width: var(--vspace-1_75);\n    }\n    .comment__meta {\n        text-align: left;\n        width: auto;\n        position: static;\n    }\n    .comment__meta .comment__time, .comment__meta .comment__reply {\n        display: inline-block;\n    }\n    .comment__meta .comment__time {\n        margin-right: 12px;\n    }\n    .comment__text {\n        padding: 0;\n    }\n    .comments-wrap .comment-respond {\n        margin-top: var(--vspace-1_5);\n    }\n}\n\n@media screen and (max-width: 800px) {\n    .comments-wrap {\n        padding: var(--vspace-2) 0 var(--vspace-1);\n    }\n    .comments-wrap .column {\n        padding: 0 var(--gutter-md);\n    }\n    .comment__avatar img {\n        margin-top: 0;\n        height: var(--vspace-1_5);\n        width: var(--vspace-1_5);\n    }\n}\n\n@media screen and (max-width: 600px) {\n    .comments-wrap h3 {\n        text-align: center;\n    }\n    .comments-wrap .column {\n        padding: 0 var(--gutter-mob);\n    }\n    .commentlist>.comment {\n        padding-top: 0;\n        padding-left: 0;\n    }\n    .comment__avatar {\n        display: none;\n    }\n    .comment ul.children li {\n        margin-top: calc(var(--vspace-1) + 0rem);\n    }\n}\n\n@media screen and (max-width: 500px) {\n    .comment ul.children li {\n        border-left: none;\n    }\n}\n\n@media screen and (max-width: 400px) {\n    .comments-wrap .column {\n        padding: 0;\n    }\n    .comment-respond form {\n        padding-top: var(--vspace-1);\n    }\n}\n\n.receive-send {\n    display: flex;\n    justify-content: space-between;\n}\n\n.receive-send a {\n    display: block;\n    color: var(--color-gray-9);\n}\n\n@media screen and (max-width: 800px) {\n    .letter-menu {\n        margin-top: 60px;\n        display: none;\n    }\n}\n\n@media screen and (min-width: 801px) {\n    .tool-letter-menu {\n        display: none;\n    }\n}\n\n.letter__toggle-menu span {\n    display: block;\n    width: 28px;\n    height: 3px;\n    margin-top: -1.5px;\n    position: absolute;\n    right: auto;\n    bottom: auto;\n    left: 50px;\n    background-color: black;\n    text-shadow: none;\n    color: transparent;\n}\n\n.letter__toggle-menu span::before,\n.letter__toggle-menu span::after {\n    content: '';\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    background-color: inherit;\n    left: 0;\n}\n\n.letter__toggle-menu span::before {\n    top: -10px;\n}\n\n.letter__toggle-menu span::after {\n    bottom: -10px;\n}\n\n.letter__overlay-close {\n    display: none;\n    text-shadow: none;\n    color: transparent;\n    width: 46px;\n    height: 46px;\n    margin-left: -23px;\n    position: absolute;\n    top: 160px;\n    left: 50%;\n}\n\n.letter__overlay-close::before,\n.letter__overlay-close::after {\n    content: '';\n    display: inline-block;\n    width: 2px;\n    height: 20px;\n    top: 12px;\n    left: 22px;\n    background-color: black;\n    position: absolute;\n}\n\n.letter__overlay-close::before {\n    -webkit-transform: rotate(45deg);\n    transform: rotate(45deg);\n}\n\n.letter__overlay-close::after {\n    -webkit-transform: rotate(-45deg);\n    transform: rotate(-45deg);\n}\n\n\n/* */\ndetails {\n    border: 1px solid #aaa;\n    border-radius: 4px;\n    padding: .5em .5em 0;\n}\n\nsummary {\n    font-weight: bold;\n    margin: -.5em -.5em 0;\n    padding: .5em;\n}\n\ndetails[open] {\n    padding: .5em;\n}\n\ndetails[open] summary {\n    border-bottom: 1px solid #aaa;\n    margin-bottom: .5em;\n}\n\n.not-watched {\n    background: var(--color-1-lighter);\n    height: 6px;\n}\n\n.hide {\n    display: none;\n}\n\n.titles-category {\n    margin-bottom: 0;\n}\n\n.horizontal {\n    background: var(--color-1);\n    height: 2px;\n    margin-top: 3px;\n    margin-bottom: 3px;\n}\n\n\n/* toogle */\n\n.toggle-button-cover\n{\n    display: table-cell;\n    position: relative;\n    width: 200px;\n    height: 140px;\n    box-sizing: border-box;\n}\n\n.button-cover\n{\n    height: 100px;\n    margin: 20px;\n    background-color: #fff;\n    box-shadow: 0 10px 20px -8px #c5d6d6;\n    border-radius: 4px;\n}\n\n.button-cover:before\n{\n    counter-increment: button-counter;\n    content: counter(button-counter);\n    position: absolute;\n    right: 0;\n    bottom: 0;\n    color: #d7e3e3;\n    font-size: 12px;\n    line-height: 1;\n    padding: 5px;\n}\n\n.button-cover, .knobs, .layer\n{\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n}\n\n.button\n{\n    position: relative;\n    top: 50%;\n    width: 86px;\n    height: 36px;\n    margin: -20px auto 0 auto;\n    overflow: hidden;\n}\n\n.button.r, .button.r .layer\n{\n    border-radius: 100px;\n}\n\n.checkbox\n{\n    position: relative;\n    width: 100%;\n    height: 100%;\n    padding: 0;\n    margin: 0;\n    opacity: 0;\n    cursor: pointer;\n    z-index: 3;\n}\n\n.knobs\n{\n    z-index: 2;\n}\n\n.layer\n{\n    width: 100%;\n    background-color: #ebf7fc;\n    transition: 0.3s ease all;\n    z-index: 1;\n}\n\n/* Button 1 */\n#button-1 .knobs:before\n{\n    content: 'От';\n    position: absolute;\n    top: 4px;\n    left: 4px;\n    width: 40px;\n    height: 26px;\n    color: #fff;\n    font-size: 10px;\n    font-weight: bold;\n    text-align: center;\n    line-height: 1;\n    padding: 9px 4px;\n    background-color: hsla(356, 74%, 72%, 1);\n    border-radius: 50%;\n    transition: 0.3s cubic-bezier(0.18, 0.89, 0.35, 1.15) all;\n}\n\n#button-1 .checkbox:checked + .knobs:before\n{\n    content: 'Мне';\n    left: 42px;\n    background-c