order: none;\n}\n\n.format-quote blockquote p,\n.format-link .link-wrap p {\n    font-family: var(--font-2);\n    font-weight: 500;\n    font-size: var(--text-lg);\n    line-height: var(--vspace-1);\n    color: black;\n    padding: 7.8rem 0 0 0;\n    margin-bottom: var(--vspace-1);\n}\n\n.format-quote cite, .format-link cite {\n    display: block;\n    font-family: var(--font-1);\n    font-size: 1.5rem;\n    font-style: normal;\n    line-height: 1.6;\n    color: var(--color-text-light);\n}\n\n.format-quote cite::before,\n.format-link cite::before {\n    display: none;\n}\n\n.format-quote cite a,\n.format-quote cite a:visited {\n    color: var(--color-text-light);\n    border: none;\n}\n\n.format-quote .entry__thumb::before {\n    /*background-image: url(../images/icons/icon-quote.svg);*/\n}\n\n.format-link .entry__thumb::before {\n    /*background-image: url(../images/icons/icon-link.svg);*/\n    width: 3.2rem;\n    height: 3.2rem;\n    background-repeat: no-repeat;\n    background-position: center;\n    background-size: 3rem 3rem;\n}\n\n.format-link .link-wrap cite:before {\n    display: none;\n}\n\n.format-link .link-wrap cite a,\n.format-link .link-wrap cite a:visited {\n    color: var(--color-text-light);\n    display: inline-block;\n    padding-bottom: .4rem;\n}\n\n.format-link .link-wrap cite a:hover,\n.format-link .link-wrap cite a:focus {\n    color: black;\n}\n\n/* format video & format audio\n * --------------------------------------- */\n.format-video .entry__thumb::after,\n.format-audio .entry__thumb::after {\n    content: \"\";\n    display: block;\n    height: 5.6rem;\n    width: 5.6rem;\n    background-position: center center;\n    background-repeat: no-repeat;\n    background-color: rgba(0, 0, 0, 0.3);\n    border-radius: 50%;\n    position: absolute;\n    top: 2.4rem;\n    left: 2.4rem;\n}\n\n.format-video .thumb-link::before,\n.format-video .thumb-link::after,\n.format-audio .thumb-link::before,\n.format-audio .thumb-link::after {\n    display: none !important;\n}\n\n.format-video .entry__thumb::after {\n    cursor: -webkit-zoom-in;\n    cursor: zoom-in;\n    /*background-image: url(../images/icons/icon-video.svg);*/\n    background-size: 2.8rem 2.8rem;\n}\n\n.format-video .entry__thumb-link {\n    cursor: -webkit-zoom-in;\n    cursor: zoom-in;\n}\n\n.format-audio .entry__thumb::after {\n    /*background-image: url(../images/icons/icon-audio.svg);*/\n    background-size: 2.8rem 2.8rem;\n}\n\n/* -------------------------------------------------------------------\n * ## format gallery\n * ------------------------------------------------------------------- */\n.format-gallery .entry__thumb {\n    overflow: hidden;\n}\n\n.format-gallery .slider {\n    overflow: hidden;\n}\n\n.format-gallery .slick-dots {\n    text-align: right;\n    margin: 0;\n    padding: 0 2.4rem 0 2rem;\n    position: absolute;\n    top: auto;\n    bottom: var(--vspace-0_75);\n    right: 0;\n}\n\n.format-gallery .slider__slides {\n    cursor: pointer;\n    overflow: hidden;\n    opacity: 0;\n    visibility: hidden;\n}\n\n.format-gallery .slider__slides.slick-initialized {\n    opacity: 1;\n    visibility: visible;\n}\n\n/* responsive:\n/* common and reusable styles\n * ------------------------------------------------------------------- */\n@media screen and (max-width: 1400px) {\n    .bricks-wrapper .entry {\n        margin-bottom: 3.6rem;\n    }\n}\n\n@media screen and (max-width: 1040px) {\n    .bricks-wrapper .entry__title,\n    .format-quote blockquote p,\n    .format-link .link-wrap p {\n        font-size: var(--text-md);\n        line-height: calc(0.875 * var(--space));\n    }\n}\n\n@media screen and (max-width: 980px) {\n    .bricks-wrapper .entry__title,\n    .format-quote blockquote p,\n    .format-link .link-wrap p {\n        font-size: var(--text-lg);\n        line-height: var(--vspace-1);\n    }\n}\n\n@media screen and (max-width: 800px) {\n    .bricks-wrapper .entry {\n        margin-bottom: 3.2rem;\n    }\n}\n\n@media screen and (max-width: 700px) {\n    .bricks-wrapper .entry {\n        margin-bottom: 2.4rem;\n    }\n    .bricks-wrapper .entry__title,\n    .format-quote blockquote p,\n    .format-link .link-wrap p {\n        font-size: var(--text-md);\n        line-height: calc(0.875 * var(--space));\n    }\n}\n\n@media screen and (max-width: 600px) {\n    .bricks-wrapper .entry {\n        margin-bottom: 4rem;\n    }\n    .bricks-wrapper .entry__title,\n    .format-quote blockquote p,\n    .format-link .link-wrap p {\n        font-size: var(--text-lg);\n        line-height: var(--vspace-1);\n    }\n    .f-slide {\n        height: 552px;\n    }\n    .f-slide__meta {\n        font-size: var(--text-sm);\n    }\n    .f-slide__title {\n        font-size: var(--text-xl);\n    }\n    .featured-post-nav button {\n        height: 5.2rem;\n        width: 5.2rem;\n        -webkit-transform: translateY(0);\n        transform: translateY(0);\n        top: auto;\n        bottom: 0;\n    }\n    .featured-post-nav button svg {\n        height: 1.6rem;\n        width: 1.6rem;\n    }\n}\n\n@media screen and (max-width: 400px) {\n    .bricks-wrapper .entry {\n        margin-bottom: 3.6rem;\n    }\n    .f-slide {\n        height: 440px;\n    }\n    .featured-post-nav button {\n        height: 4.4rem;\n        width: 4.4rem;\n    }\n    .featured-post-nav button svg {\n        height: 1.4rem;\n        width: 1.4rem;\n    }\n}\n\n\n\n\n\n\n\n/* ===================================================================\n * # pageheader\n *\n * ------------------------------------------------------------------- */\n.s-pageheader {\n    padding: var(--vspace-1_5) 0 0 0;\n    text-align: center;\n}\n\n.s-pageheader .row {\n    max-width: 900px;\n}\n\n.s-pageheader h1 {\n    margin-top: 0;\n    margin-bottom: var(--vspace-1);\n}\n\n\n\n/* ===================================================================\n * # content wrap\n *\n * ------------------------------------------------------------------- */\n.s-content {\n    --row-max-width: 1080px;\n    padding-top: var(--topspace);\n    padding-bottom: var(--vspace-0_125);\n}\n\n.s-content--single {\n    padding-bottom: var(--vspace-0_125);\n}\n\n.s-content .row {\n    max-width: var(--row-max-width);\n}\n\n.s-content .row.wide {\n    max-width: 1400px;\n}\n\n.s-content .row.narrow {\n    max-width: 800px;\n}\n\n.s-content__media {\n    position: relative;\n    margin-bottom: var(--vspace-2_5);\n}\n\n.s-content__media img {\n    vertical-align: bottom;\n    margin-bottom: 0;\n}\n\n.s-content__primary {\n    padding-left: 20rem;\n    padding-right: 20rem;\n}\n\n.s-content__primary img {\n    max-width: calc(var(--row-max-width) - var(--gutter-lg) * 2);\n    margin: var(--vspace-1) 0 var(--vspace-1) -8rem;\n}\n\n.s-content__title {\n    text-align: center;\n}\n\n.s-content__title--post {\n    margin-bottom: var(--vspace-0_25);\n}\n\n.s-content__blocks h4 {\n    margin-top: 0;\n}\n\n.s-content__form {\n    margin-top: var(--vspace-2);\n}\n\n/* responsive:\n * content wrap\n * ------------------------------------------------------------------- */\n@media screen and (max-width: 1200px) {\n    .s-content {\n        --row-max-width: 1000px;\n    }\n    .s-content__primary {\n        padding-left: 4.8rem;\n        padding-right: 4.8rem;\n    }\n    .s-content__primary img {\n        max-width: calc(var(--row-max-width) - var(--gutter-md) * 2);\n        margin: var(--vspace-1) 0 var(--vspace-1) -4.8rem;\n    }\n}\n\n@media screen and (max-width: 1100px) {\n    .s-content {\n        --row-max-width: 920px;\n    }\n    .s-content__primary {\n        padding-left: 4rem;\n        padding-right: 4rem;\n    }\n    .s-content__primary img {\n        margin: var(--vspace-1) 0 var(--vspace-1) -4rem;\n    }\n}\n\n@media screen and (max-width: 1020px) {\n    .s-content__primary {\n        padding-left: 3.2rem;\n        padding-right: 3.2rem;\n    }\n    .s-content__primary img {\n        max-width: 100%;\n        margin: var(--vspace-1) 0;\n    }\n}\n\n@media screen and (max-width: 800px) {\n    .s-content {\n        padding-top: var(--topspace);\n    }\n    .s-content__primary {\n        padding-left: 0;\n        padding-right: 0;\n    }\n    .s-content__media {\n        margin-bottom: var(--vspace-2);\n    }\n}\n\n@media screen and (max-width: 600px) {\n    .s-content {\n        padding-top: var(--topspace);\n    }\n    .s-content__media {\n        margin-bottom: var(--vspace-1_5);\n    }\n}\n\n@media screen and (max-width: 400px) {\n    .s-content__title {\n        font-size: var(--text-xxl);\n        line-height: var(--vspace-1_5);\n    }\n}\n\n\n\n/* ===================================================================\n * # bricks masonry\n *\n * ------------------------------------------------------------------- */\n.s-bricks {\n    padding-top: 4.2rem;\n}\n\n.s-bricks.with-top-sep {\n    position: relative;\n}\n\n.s-bricks.with-top-sep::before {\n    content: \"\";\n    display: block;\n    position: absolute;\n    left: 50%;\n    top: 0;\n    margin-left: -100px;\n    width: 200px;\n    height: 1px;\n    background-color: var(--color-border);\n}\n\n.s-bricks .masonry {\n    max-width: 1440px;\n    width: 94%;\n    margin: 0 auto var(--vspace-1);\n}\n\n.s-bricks .pagination {\n    margin-top: 6rem;\n}\n\n.bricks-wrapper .grid-sizer,\n.bricks-wrapper .brick {\n    width: 100%;\n}\n\n.bricks-wrapper .brick {\n    padding: 0;\n}\n\n.bricks-wrapper .featured-grid {\n    width: 100%;\n}\n\n.bricks-wrapper .featured-grid .entry-content {\n    width: 100%;\n    background: #151515;\n}\n\n/* responsive:\n/* bricks masonry\n * ------------------------------------------------------------------- */\n@media screen and (max-width: 1440px) {\n    .s-bricks .masonry {\n        width: 98%;\n    }\n}\n\n@media screen and (max-width: 1400px) {\n    .s-bricks .masonry {\n        max-width: var(--width-max);\n        width: 94%;\n    }\n    .bricks-wrapper .grid-sizer,\n    .bricks-wrapper .brick {\n        width: 100%;\n    }\n    .bricks-wrapper .brick {\n        padding: 0;\n    }\n    .bricks-wrapper .featured-grid {\n        width: 100%;\n    }\n}\n\n@media screen and (max-width: 980px) {\n    .s-bricks .masonry {\n        max-width: 780px;\n        width: 96%;\n    }\n    .bricks-wrapper .grid-sizer,\n    .bricks-wrapper .brick {\n        width: 100%;\n    }\n    .bricks-wrapper .featured-grid {\n        width: 100%;\n    }\n}\n\n@media screen and (max-width: 800px) {\n    .bricks-wrapper .brick {\n        padding: 0;\n    }\n}\n\n@media screen and (max-width: 700px) {\n    .bricks-wrapper .brick {\n        padding: 0;\n    }\n}\n\n@media screen and (max-width: 600px) {\n    .s-bricks .masonry {\n        max-width: 480px;\n        width: 100%;\n        padding-left: 6vw;\n        padding-right: 6vw;\n    }\n    .bricks-wrapper .grid-sizer,\n    .bricks-wrapper .brick {\n        float: none;\n        width: 100%;\n        padding: 0 var(--gutter-mob);\n        clear: both;\n    }\n}\n\n@media screen and (max-width: 400px) {\n    .bricks-wrapper .brick {\n        margin-left: 0;\n        margin-right: 0;\n        padding: 0;\n    }\n}\n\n\n\n/* ===================================================================\n * # footer\n *\n * ------------------------------------------------------------------- */\n.s-footer {\n    padding-top: var(--vspace-2_5);\n    padding-bottom: var(--vspace-2);\n    font-size: 1.6rem;\n    line-height: 2.8rem;\n    color: var(--color-text-light);\n}\n\n.s-footer .row {\n    max-width: 1240px;\n}\n\n.s-footer [class*=\"column\"] {\n    margin-bottom: var(--vspace-0_5);\n}\n\n.s-footer a {\n    color: black;\n}\n\n.s-footer h5 {\n    font-size: var(--text-size);\n    font-weight: 500;\n    padding-bottom: var(--vspace-0_75);\n    margin-bottom: var(--vspace-0_75);\n    margin-top: 0;\n    border-bottom: 1px solid var(--color-border);\n}\n\n.s-footer ul {\n    margin-left: 0;\n}\n\n.s-footer li {\n    padding-left: 0;\n}\n\n/* -------------------------------------------------------------------\n * ## footer main\n * ------------------------------------------------------------------- */\n.s-footer__main {\n    padding-bottom: var(--vspace-0_5);\n}\n\n.s-footer__main ul li {\n    list-style: none;\n}\n\n.s-footer__main ul a {\n    color: var(--color-text-light);\n}\n\n.s-footer__main ul a:hover, .s-footer__main ul a:focus {\n    color: black;\n}\n\n/* footer subscribe\n * -------------------------------------- */\n.s-footer__subscribe p {\n    margin-bottom: var(--vspace-0_5);\n}\n\n.s-footer__subscribe .subscribe-form #mc-form {\n    padding: 0;\n}\n\n.s-footer__subscribe .subscribe-form input[type=\"email\"] {\n    padding: .8rem 2rem;\n    border: none;\n    width: 100%;\n    margin-bottom: var(--vspace-0_5);\n}\n\n.s-footer__subscribe .subscribe-form input[type=\"submit\"] {\n    display: none;\n}\n\n.s-footer__subscribe .subscribe-form .subscribe-message {\n    font-family: var(--font-1);\n    font-size: var(--text-sm);\n    color: black;\n}\n\n.s-footer__subscribe .subscribe-form .subscribe-message i {\n    color: black;\n    margin-right: .4rem;\n}\n\n/* -------------------------------------------------------------------\n * ## footer bottom\n * ------------------------------------------------------------------- */\n.s-footer__bottom {\n    margin-top: var(--vspace-0_5);\n    text-align: center;\n}\n\n/* copyright\n * -------------------------------------- */\n.ss-copyright span {\n    display: inline-block;\n    font-size: var(--text-sm);\n    line-height: 2.4rem;\n}\n\n.ss-copyright span::after {\n    content: \"|\";\n    display: inline-block;\n    padding: 0 1rem 0 1.2rem;\n    color: rgba(0, 0, 0, 0.2);\n}\n\n.ss-copyright span:last-child::after {\n    display: none;\n}\n\n/* -------------------------------------------------------------------\n * ## go top\n * ------------------------------------------------------------------- */\n.ss-go-top {\n    z-index: 2;\n    opacity: 0;\n    visibility: hidden;\n    -webkit-transform: translate3d(0, 200%, 0);\n    transform: translate3d(0, 200%, 0);\n    -webkit-transition: all 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);\n    transition: all 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);\n    position: fixed;\n    bottom: 6.4rem;\n    right: 6.4rem;\n}\n\n.ss-go-top a {\n    display: -ms-flexbox;\n    display: -webkit-box;\n    display: flex;\n    -ms-flex-align: center;\n    -webkit-box-align: center;\n    align-items: center;\n    -ms-flex-pack: center;\n    -webkit-box-pack: center;\n    justify-content: center;\n    text-decoration: none;\n    border: none;\n    height: 6.4rem;\n    width: 6.4rem;\n    border-radius: 50%;\n    background-color: var(--color-gray-3);\n    -webkit-transition: all .3s;\n    transition: all .3s;\n    position: relative;\n}\n\n.ss-go-top a:hover, .ss-go-top a:focus {\n    background-color: black;\n}\n\n.ss-go-top a:hover svg path, .ss-go-top a:focus svg path {\n    fill: white;\n}\n\n.ss-go-top svg {\n    height: 2.4rem;\n    width: 2.4rem;\n}\n\n.ss-go-top svg path {\n    fill: black;\n}\n\n.ss-go-top.link-is-visible {\n    opacity: 1;\n    visibility: visible;\n    -webkit-transform: translate3d(0, 0, 0);\n    transform: translate3d(0, 0, 0);\n}\n\n/* responsive:\n * footer\n * ------------------------------------------------------------------- */\n@media screen and (max-width: 800px) {\n    .s-footer {\n        padding-bottom: var(--vspace-1_5);\n    }\n    .ss-go-top {\n        right: 3.6rem;\n        bottom: 4.8rem;\n    }\n    .ss-go-top a {\n        height: 5.2rem;\n        width: 5.2rem;\n    }\n    .ss-go-top svg {\n        height: 2rem;\n        width: 2rem;\n    }\n}\n\n@media screen and (max-width: 600px) {\n    .s-footer__bottom {\n        padding-bottom: .8rem;\n    }\n    .ss-copyright span {\n        display: block;\n    }\n    .ss-copyright span::after {\n        display: none;\n    }\n    .ss-go-top {\n        right: 2.4rem;\n        bottom: 4rem;\n    }\n}\n\n\n\n/* ===================================================================\n * # blog styles and blog components\n *\n * ------------------------------------------------------------------- */\n\n/* -------------------------------------------------------------------\n * ## single post meta\n * ------------------------------------------------------------------- */\n.s-content__post-meta {\n    font-family: var(--font-1);\n    font-size: calc(var(--text-size) * 0.944);\n    list-style: none;\n    text-align: center;\n    margin-left: 0;\n    margin-bottom: var(--vspace-1_75);\n    color: var(--color-text-light);\n}\n\n.s-content__post-meta a {\n    color: black;\n}\n\n.s-content__post-meta li {\n    display: inline-block;\n    padding-left: 0;\n    margin: 0 .8rem;\n}\n\n.s-content__post-meta .cat a {\n    margin-right: .6rem;\n}\n\n.s-content__post-meta .cat a:last-child {\n    margin-right: 0;\n}\n\n.s-content__post-meta .cat a:last-child::after {\n    content: none;\n}\n\n.s-content__post-meta .cat a::after {\n    content: \",\";\n}\n\n/* -------------------------------------------------------------------\n * ## single post tags\n * ------------------------------------------------------------------- */\n.s-content__post-tags {\n    margin: var(--vspace-2) 0 var(--vspace-1_25);\n}\n\n.s-content__post-tags span {\n    color: black;\n    margin-right: 1rem;\n    font-weight: 600;\n}\n\n.s-content__post-tags a {\n    color: var(--color-text-light);\n    margin-right: 1rem;\n}\n\n/* -------------------------------------------------------------------\n * ## author profile\n * ------------------------------------------------------------------- */\n.s-content__author {\n    margin-top: var(--vspace-2_5);\n    padding-top: var(--vspace-0_25);\n    padding-left: 9.6rem;\n    position: relative;\n}\n\n.s-content__author a {\n    color: black;\n}\n\n.s-content__author h5 {\n    margin-top: 0;\n}\n\n.s-content__author img {\n    margin: .6rem 0 0 0 !important;\n    width: 6.4rem;\n    height: 6.4rem;\n    border-radius: 50%;\n    position: absolute;\n    left: 0;\n    top: 0;\n}\n\n.s-content__author p {\n    margin-bottom: var(--vspace-0_5);\n}\n\n.s-content__author .author-social {\n    list-style: none;\n    margin-left: 0;\n    font-size: var(--text-xs);\n    text-transform: uppercase;\n    letter-spacing: .2em;\n}\n\n.s-content__author .author-social li {\n    display: inline-block;\n    margin-right: 0.8rem;\n    padding-left: 0;\n}\n\n.s-content__author .author-social a {\n    color: var(--color-text-light);\n}\n\n/* -------------------------------------------------------------------\n * ## single post page nav\n * ------------------------------------------------------------------- */\n.s-content__pagenav {\n    padding-top: var(--vspace-0_5);\n    margin: 12rem 0;\n    border-top: 1px solid var(--color-border);\n}\n\n.s-content__pagenav div {\n    float: left;\n    width: 50%;\n    padding-top: var(--vspace-0_5);\n    padding-right: var(--gutter-lg);\n}\n\n.s-content__pagenav a {\n    font-family: var(--font-2);\n    font-weight: 600;\n    font-size: var(--text-lg);\n    line-height: var(--vspace-1);\n    border: none;\n    color: black;\n}\n\n.s-content__pagenav a span {\n    display: block;\n    font-family: var(--font-1);\n    font-weight: 400;\n    font-size: var(--text-sm);\n    line-height: var(--vspace-1);\n    margin-bottom: var(--vspace-0_5);\n    color: var(--color-text-light);\n    -webkit-transition: all 0.3s ease-in-out;\n    transition: all 0.3s ease-in-out;\n}\n\n.s-content__pagenav a:hover span {\n    color: black;\n}\n\n/* -------------------------------------------------------------------\n * responsive:\n * blog styles and blog components\n * ------------------------------------------------------------------- */\n@media screen and (max-width: 800px) {\n    .s-content__author {\n        padding-left: 0;\n        text-align: center;\n    }\n    .s-content__author img {\n        position: static;\n        width: 6.6rem;\n        height: 6.6rem;\n 